// UseRef 
import { useRef, useState } from 'react';
import { CommonPrism } from '../../Common';

export const UseRefFC = () => {

    const myRef = useRef<HTMLDivElement>(null);
    //  ref  myRef.current 
    function handleClick() {
        if (myRef.current) {
            myRef.current.style.color = 'red';
        }
    }

    //  useRef  setTimeout  id
    const [seconds, setSeconds] = useState(0);
    const intervalRef = useRef<number>(0);
    const isRunning = useRef<boolean>(false);


    function startTimer() {
        if (!isRunning.current) {
            isRunning.current = true;
            intervalRef.current = window.setInterval(() => {
                setSeconds((prevSeconds) => prevSeconds + 1);
            }, 1000);
        }
    }

    function stopTimer() {
        clearInterval(intervalRef.current);
        isRunning.current = false;
    }


    return (
        <div>
            <h1 id='useRef'>useRef</h1>
            <p>useRef  React Hook(reference) </p>
            <p>useRef  ref </p>
            <p>const myRef = useRef(initialState null);<br />
                 ref DOM</p>
            <p>ref()ref{`<div ref={myRef}></div>`} </p>

            <h2>Example</h2>
            <CommonPrism>
                {`const myRef = useRef<HTMLDivElement>(null);`}
            </CommonPrism>

            <p>ref</p>
            <CommonPrism>
                {`<div ref={myRef}></div>`}
            </CommonPrism>

            <p> myRef.current </p>            
            <CommonPrism>
                {`function handleClick() {
    if (myRef.current) {
        myRef.current.style.color = 'red';
    }
}`}
            </CommonPrism>


            <div ref={myRef}>
                <p></p>
                <button onClick={handleClick}>Click Me</button>
            </div>

            <p> {`
                useRef  React  ref  JSX  ref = {myRef} .
                useRef  current   current 
                `}
            </p>

            <h2>Example </h2>
            <p>useRef  setTimeout  id DOM </p>
            <p> Timer<br />
                Start <br />
                Stop</p>
            <div>
                <p>{seconds} seconds (useState) </p>
                <button onClick={startTimer}>Start</button>
                <button onClick={stopTimer}>Stop</button>
            </div>
            {/* <UseReFCTimer /> */}


        </div>
    );
};

export const UseReFCTimer = () => {
    //  useRef  setTimeout  id
    const secondsRef = useRef<number>(0);
    const intervalRef = useRef<number>(0);
    const isRunning = useRef<boolean>(false);

    //  useState
    const [count, setCount] = useState(0);

    // 
    function startTimer() {
        if (!isRunning.current) {
            isRunning.current = true;
            intervalRef.current = window.setInterval(() => {
                secondsRef.current++;
            }, 1000);
        }
    }

    // 
    function stopTimer() {
        clearInterval(intervalRef.current);
        isRunning.current = false;
    }

    return (
        <>
            <div>
                <p>{secondsRef.current} seconds (useRef) </p>
                <button onClick={startTimer}>Start</button>
                <button onClick={stopTimer}>Stop</button>

                <button onClick={() => setCount(count + 1)}></button>
            </div>
            <div>
                <CommonPrism>
                    {`
//  useRef  setTimeout  id
const secondsRef = useRef<number>(0);
const intervalRef = useRef<number>(0);
const isRunning = useRef<boolean>(false);
//  useState
const [count, setCount] = useState(0);
// 
function startTimer() {
    if (!isRunning.current) {
        isRunning.current = true;
        intervalRef.current = window.setInterval(() => {
            secondsRef.current++;
        }, 1000);
    }
}
// 
function stopTimer() {
    clearInterval(intervalRef.current);
    isRunning.current = false;
}
                `}
                </CommonPrism>
            </div>
        </>
    );
}