// UseEffect 
import React, { useState, useEffect } from 'react';
import ClearButton from '../ClearButton';
import { CommonPrism } from '../../Common';

export const UseEffectFC = () => {
    const [count, setCount] = useState(0);
    useEffect(() => {
        console.log('useEffect without arraycomponentrender');
    });
    useEffect(() => {
        console.log('useEffect with empty array');
    }, []);
    useEffect(() => {
        console.log("count")
        console.log('useEffect with countcountcount')
    }, [count]);

    return (
        <div>
            <h1 id="useEffect">useEffect</h1>
            <p>useEffectReact Hook</p>
            <p>DOM</p>
            <p>{`useEffect(() => {}, [])`}</p>
            <p></p>
            <p></p>
            <p></p>

            <h2>Example</h2>
            <CommonPrism>
                {`useEffect(() => {}, [])`}
            </CommonPrism>
            
            <CommonPrism>
                {`
// 
useEffect(() => {
    console.log('useEffect without array');
});`}
            </CommonPrism>

            <CommonPrism>
                {`
// 
useEffect(() => {
    console.log('useEffect with empty array');
}, []);`}
            </CommonPrism>

            <CommonPrism>
                {`
// 
useEffect(() => {
    console.log('useEffect with count');
}, [count]);`}
            </CommonPrism>

            <button onClick={() => setCount((count) => count + 1)}>count</button>
            <ClearButton />

        </div >
    );
};
