// UseReducer 
import { useReducer, useState } from "react";
import { CommonPrism } from '../../Common';

import uuid from "react-uuid";
import { Action2, ActionType2, reducer2 } from "./Reducer2";

// stateinterface
type State = {
    count: number,
    string: string,
}

// ActiontypeenumINCREASEDECREASE
enum ActionType {
    INCREASE = "increase",
    DECREASE = "decrease",
}

// actioninterface
interface Action {
    type: ActionType; // typeenumINCREASEDECREASE
    payload: number; // payload
}

function reducer(state: State, action: Action) {
    const { type, payload } = action;
    switch (type) {
        case ActionType.INCREASE:
            return {
                ...state,
                count: state.count + payload,
            };
        case ActionType.DECREASE:
            return {
                ...state,
                count: state.count - payload,
            };
        default:
            return state;
    }
}

function increment(dispatch: React.Dispatch<Action>) {
    dispatch({ type: ActionType.INCREASE, payload: 1 });
    // dispatchreducer
    //  case ActionType.INCREASE   payload  1
    //  state.count + 1
}
function decrement(dispatch: React.Dispatch<Action>) {
    dispatch({ type: ActionType.DECREASE, payload: 1 });
}


// Action 2
function toDo_add(dispatch2: React.Dispatch<Action2>, text: string) {
    dispatch2({ type: ActionType2.ADD, payload: { text } });
}

function toDo_delete(dispatch2: React.Dispatch<Action2>, index: number) {
    dispatch2({ type: ActionType2.DELETE, payload: index });
}

export const UseReducerFC = () => {

    const [state, dispatch] = useReducer(reducer, { count: 1, string: "hello" });
    const [state2, dispatch2] = useReducer(reducer2, { value: ["hello", "world"] });
    const [input, setInput] = useState("")

    const handleOnChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        setInput(e.target.value);
    }

    return (
        <div>
            <h1 id="useReducer">useReducer</h1>
            <p>useReducerhookreducerstatedispatch</p>
            <p>useReducerredux</p>
            <p>useReducerreducerstateaction</p>
            <p>useReducerdispatchactionactionreduceractionstate</p>

            <h3></h3>
            <h2>Example</h2>
            <p>reducerstateaction</p>
            <p>stateaction</p>
            <CommonPrism>
                {`// stateinterface
type State = {
    count: number,
    string: string,
}

// ActiontypeenumINCREASEDECREASE
enum ActionType {
    INCREASE = "increase",
    DECREASE = "decrease",
}

// actioninterface
interface Action {
    type: ActionType; // typeenumINCREASEDECREASE
    payload: number; // payload
}

function reducer(state: State, action: Action) {
    const { type, payload } = action;
    switch (type) {
        case ActionType.INCREASE:
            return {
                ...state,
                count: state.count + payload,
            };
        case ActionType.DECREASE:
            return {
                ...state,
                count: state.count - payload,
            };
        default:
            return state;
    }
}`}
            </CommonPrism>

            <p>componentuseReducerreducerstatedispatch</p>
            <CommonPrism>
                {`const [state, dispatch] = useReducer(reducer, { count: 0 });`}
            </CommonPrism>

            <p>componentdispatchactionactionreduceractionstate</p>
            <CommonPrism>
                {`function increment(dispatch: React.Dispatch<Action>) {
    dispatch({ type: ActionType.INCREASE, payload: 1 });
    // dispatchreducer
    //  case ActionType.INCREASE   payload  1
    //  state.count + 1
}

function decrement(dispatch: any) {
    dispatch({ type: "decrease", payload: 1 });
}`}
            </CommonPrism>

            <h2></h2>
            <p>{state.count} {state.string}</p>
            <button onClick={() => increment(dispatch)}>+</button>
            <button onClick={() => decrement(dispatch)}>-</button>

            <h2>TODO</h2>
            <div className="border">
                <input type="text" onChange={handleOnChange} placeholder="text"/>
                <button onClick={() => toDo_add(dispatch2, input)}></button>

                {state2.value.map((item, index) => {
                    return (
                        <div key={uuid()}>
                            <p className="border margin">{item}</p>
                            <button onClick={() => toDo_delete(dispatch2, index)}></button>
                        </div>
                    );
                })}
            </div>
        </div>
    );
};