import { useEffect, useState } from "react"
type ListProps = {
    getItems: () => number[];
}
//  List.js 
export const List: React.FC<ListProps> = ({ getItems }) => {
    //  getItems  items 
    const [items, setItems] = useState<number[]>([])

    useEffect(() => {
        setItems(getItems())
        // console.log('Updating Items')
    }, [getItems])


    return (
        <ul>
            {items.map((item, index) => (
                <li key={index}>{item}</li>
            ))}
        </ul>
    );
};
